'use client';

import { useEffect, useState } from 'react';
import supabase from '@/lib/supabase';
import { Box, Cpu  } from 'lucide-react';

interface Product {
  id: number;
  name: string;
  sku: string;
  description: string;
  category_id: number | null;
  supplier_id: number | null;
  quantity: number;
  threshold: number;
  icon_name: string;
}

const IconMap = {
  Box: <Box size={16} />,

  Chip: <Cpu  size={16} />,
};

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [categories, setCategories] = useState<{ id: number; name: string }[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [modalOpen, setModalOpen] = useState(false);

  const [form, setForm] = useState({
    name: '',
    sku: '',
    description: '',
    category_id: null as number | null,
    icon_name: 'Box',
    quantity: 0,
    threshold: 5,
  });

  useEffect(() => {
    const fetchProducts = async () => {
      const { data, error } = await supabase.from('products').select('*');
      if (error) console.error(error.message);
      else setProducts(data);
    };

    const fetchCategories = async () => {
      const { data, error } = await supabase.from('categories').select('id, name');
      if (!error && data) {
        setCategories(data);
      }
    };

    fetchProducts();
    fetchCategories();
  }, []);

  const handleAddProduct = async () => {
    const { name, sku, quantity, threshold } = form;
    if (!name || !sku || quantity == null || threshold == null) {
      return alert('Please fill in all required fields.');
    }

    const { data, error } = await supabase.from('products').insert([form]).select();
    if (error) return alert(error.message);
    setProducts((prev) => [...prev, ...(data || [])]);
    setForm({
      name: '',
      sku: '',
      description: '',
      category_id: null,
      icon_name: 'Box',
      quantity: 0,
      threshold: 5,
    });
    setModalOpen(false);
  };

  const filteredProducts = products.filter((p) =>
    p.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-semibold">Products</h1>
        <div className="flex gap-2">
          <input
            type="text"
            placeholder="Search products..."
            className="border rounded px-3 py-2 text-sm"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <select className="border rounded px-3 py-2 text-sm">
            <option>All Categories</option>
            {categories.map((cat) => (
              <option key={cat.id}>{cat.name}</option>
            ))}
          </select>
          <button
            onClick={() => setModalOpen(true)}
            className="bg-blue-600 text-white px-4 py-2 rounded text-sm"
          >
            + Add Product
          </button>
        </div>
      </div>

      <div className="border rounded overflow-hidden">
        <table className="w-full text-sm text-left">
          <thead className="bg-gray-100">
            <tr>
              <th className="p-3">Product</th>
              <th className="p-3">Category ID</th>
              <th className="p-3">Quantity</th>
              <th className="p-3">Low Stock Alert</th>
              <th className="p-3">Status</th>
              <th className="p-3">Actions</th>
            </tr>
          </thead>
          <tbody>
            {filteredProducts.length === 0 ? (
              <tr>
                <td colSpan={6} className="text-center text-gray-500 py-8">
                  No products found
                </td>
              </tr>
            ) : (
              filteredProducts.map((p) => (
                <tr key={p.id} className="border-t">
                  <td className="p-3 flex items-center gap-2">
                    {IconMap[p.icon_name as keyof typeof IconMap]} {p.name}
                  </td>
                  <td className="p-3">{p.category_id ?? 'â€”'}</td>
                  <td className="p-3">{p.quantity}</td>
                  <td className="p-3">
                    {p.quantity <= p.threshold ? (
                      <span className="text-red-500">Yes</span>
                    ) : (
                      <span className="text-green-500">No</span>
                    )}
                  </td>
                  <td className="p-3 capitalize">active</td>
                  <td className="p-3 text-right">
                    <button className="text-blue-600 hover:underline text-sm">Edit</button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {modalOpen && (
        <div className="fixed inset-0 bg-black/40 flex items-center justify-center z-50">
          <div className="bg-white p-6 rounded shadow-xl w-full max-w-lg">
            <h2 className="text-lg font-bold mb-2">Add New Product</h2>

            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="text-sm font-medium block mb-1">Product Name *</label>
                <input
                  className="border px-3 py-2 w-full rounded"
                  value={form.name}
                  onChange={(e) => setForm({ ...form, name: e.target.value })}
                />
              </div>
              <div>
                <label className="text-sm font-medium block mb-1">SKU *</label>
                <input
                  className="border px-3 py-2 w-full rounded"
                  value={form.sku}
                  onChange={(e) => setForm({ ...form, sku: e.target.value })}
                />
              </div>

              <div>
                <label className="text-sm font-medium block mb-1">Category</label>
                <select
                  className="border px-3 py-2 w-full rounded"
                  value={form.category_id ?? ''}
                  onChange={(e) => setForm({ ...form, category_id: +e.target.value || null })}
                >
                  <option value="">Select category</option>
                  {categories.map((cat) => (
                    <option key={cat.id} value={cat.id}>
                      {cat.name}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="text-sm font-medium block mb-1">Icon</label>
                <select
                  className="border px-3 py-2 w-full rounded"
                  value={form.icon_name}
                  onChange={(e) => setForm({ ...form, icon_name: e.target.value })}
                >
                  <option>Box</option>                  
                  <option>Chip</option>
                </select>
              </div>

              <div>
                <label className="text-sm font-medium block mb-1">Quantity *</label>
                <input
                  type="number"
                  className="border px-3 py-2 w-full rounded"
                  value={form.quantity}
                  onChange={(e) => setForm({ ...form, quantity: +e.target.value })}
                />
              </div>

              <div>
                <label className="text-sm font-medium block mb-1">Threshold *</label>
                <input
                  type="number"
                  className="border px-3 py-2 w-full rounded"
                  value={form.threshold}
                  onChange={(e) => setForm({ ...form, threshold: +e.target.value })}
                />
              </div>
            </div>

            <div className="mt-4">
              <label className="text-sm font-medium block mb-1">Description</label>
              <textarea
                className="border px-3 py-2 w-full rounded min-h-[80px]"
                value={form.description}
                onChange={(e) => setForm({ ...form, description: e.target.value })}
              />
            </div>

            <div className="mt-6 flex justify-end gap-2">
              <button onClick={() => setModalOpen(false)} className="border px-4 py-2 rounded">
                Cancel
              </button>
              <button onClick={handleAddProduct} className="bg-blue-600 text-white px-4 py-2 rounded">
                Add Product
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
